import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { TopComponent } from './top/top.component';

import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { HttpClientModule, HttpClient } from '@angular/common/http';

//插件库
import { MatSliderModule } from '@angular/material/slider';
import { MatButtonModule } from '@angular/material/button';
import { MatRippleModule } from '@angular/material/core';
import {MatSnackBarModule} from '@angular/material/snack-bar';

import { AboutComponent } from './about/about/about.component';
import { IndexComponent } from './index/index.component';
import { MeComponent } from './me/me/me.component';
import { ResumeComponent } from './resume/resume/resume.component';
import { ProjectComponent } from './project/project/project.component';
import { LabComponent } from './lab/lab/lab.component';
import { ShareComponent } from './share/share/share.component';
import { BottomComponent } from './bottom/bottom/bottom.component';
// import { SwiperModule } from "swiper/angular";

export function createTranslateHttpLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    AppComponent,
    TopComponent,
    AboutComponent,
    IndexComponent,
    MeComponent,
    ResumeComponent,
    ProjectComponent,
    LabComponent,
    ShareComponent,
    BottomComponent,
  ],
  imports: [
    MatSliderModule,
    MatButtonModule,
    MatRippleModule,
    MatSnackBarModule,
    // SwiperModule,
    
    FormsModule,
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: createTranslateHttpLoader,
        deps: [HttpClient],
      },
    }),
  ],
  providers: [],
  bootstrap: [AppComponent],
})
export class AppModule {}
